<template>
  <form @submit.prevent="handleSubmit">
    <h3>Create you playlist</h3>
    <input type="text" required placeholder="Playlist Name" v-model="title" />
    <textarea
      required
      placeholder="Playlist Description..."
      v-model="description"
    ></textarea>
    <label>upload you playlist cover</label>
    <input type="file" @change="handleChange" />
    <div class="error">{{ fileError }}</div>
    <button v-if="!isPending">submit</button>
    <button v-else disabled>saving...</button>
  </form>
</template>

<script>
import { ref } from "vue";
import useStorage from "@/composables/useStorage";
import useCollection from "@/composables/useCollection";
import getUser from "@/composables/getUser";
import { timestamp } from "@/firebase/config";
import { useRouter } from "vue-router";
export default {
  setup() {
    const title = ref("");
    const description = ref("");
    const file = ref(null);
    const fileError = ref(null);
    const { filePath, url, uploadImage } = useStorage();
    const { error, addDoc } = useCollection("playlists");
    const { user } = getUser();
    const isPending = ref(false);
    const router = useRouter();

    let types = ["image/png", "image/jpeg", "image/jpg"];
    const handleSubmit = async () => {
      if (file.value) {
        isPending.value = true;
        await uploadImage(file.value);
        const res = await addDoc({
          title: title.value,
          description: description.value,
          userId: user.value.uid,
          userName: user.value.displayName,
          filePath: filePath.value,
          coverUrl: url.value,
          createdAt: timestamp(),
          songs: [],
        });
        isPending.value = false;
        if (!error.value) {
          router.push({ name: "PlaylistDetails", params: { id: res.id } });
        }
      }
    };
    const handleChange = (e) => {
      const selected = e.target.files[0];
      if (selected && types.includes(selected.type)) {
        file.value = selected;
        fileError.value = null;
      } else {
        fileError.value = "please select an png or jpeg or jpg image  ";
        file.value = null;
      }
    };
    return {
      title,
      description,
      handleSubmit,
      handleChange,
      fileError,
      file,
      isPending,
    };
  },
};
</script>

<style>
form {
  background: white;
}
input[type="file"] {
  border: 0;
  padding: 0;
}
label {
  font-size: 14px;
  display: block;
  margin-top: 30px;
}
button {
  margin-top: 20px;
}
</style>
